import React from "react";

export interface IframeProps {
  className?: string;
  inlineStyles?: { [key: string]: string };
  src: string;
  height?: string;
  width?: string;
  allow?: string;
  allowFullScreen?: boolean;
  isVideo?: boolean;
  title?: string;
}

export default (props: IframeProps) => {
  const {
    className,
    inlineStyles,
    src,
    height,
    width,
    allow,
    allowFullScreen,
    isVideo,
    title,
  } = props;

  return (
    <div className={"container video"}>
      <iframe
        className="iframe"
        allow={allow}
        src={src}
        style={{ ...inlineStyles }}
        height={height}
        width={width}
        allowFullScreen={allowFullScreen}
        title={title}
      />
    </div>
  );
};
